<template>
     <div class="tab-bar">
         <!-- logo部分 -->
          <div class="logo-box">
              <img :src="require('../static/image/symbol2.jpg')" alt="" class="logo">
          </div>
          <!-- logo部分结束 -->
          <!-- tab-item部分 -->
          <div class="tab-item-list" >
              <div :class="isActive(index)" 
              v-for="(item,index) in list" :key="item.title" 
              @click="indexChange(index)">
                     <i :class="item.icon"></i>
                    {{item.title}}
              </div>
          </div>
          <!-- tab-item部分结束-->
          <!-- 用户信息部分 -->
          <div class="userinfo-box" >
             <img  class="avatar" src="https://guli-education-project-mbj.oss-cn-beijing.aliyuncs.com/2021/06/04/554d19465b7c48268d37b28dfb8b3a242f57d53f8794a4c20c038a1201f41bd5ac6e39c2.jpg?versionId=CAEQDRiBgIC36pbSzhciIDBmMDQ3NGRiMjViYTQyNjBhNDcxMTk3MzFkODdiYTQ1" alt="">
             <span class="user-name">Yballul</span>
          </div> 
          <!-- 用户信息部分结束-->
     </div>
</template>

<script>
export default {
    props:{
          list:{
              type:Array,
              default(){
              return [
              {
                title:'训练教程',
                icon:'el-icon-document'
              },
              {
                title:'查看公告',
                icon:'el-icon-alarm-clock'
              },
              {
                title:'个人资料',
                icon:'el-icon-user'
              }]
              }
          },
          currentIndex:{
              type:Number,
              default:0
          }
    },
    data(){
        return{
        }
    },
    methods:{
        isActive(index){
           return index==this.currentIndex?{'tab-item':true,'tab-item-active':true}:{'tab-item':true};
        },
        indexChange(index)
        {
            this.$emit('indexChange',{index});
        }
    }
}
</script>

<style scoped>
    .tab-bar
    {
        width: 100%;
        position:fixed;
        height: 60px;
        left:0;
        top:0;
        background-color: #D893F5;
        line-height: 60px;
        color:white;
        display: flex;
        z-index:100;
    }
    .logo
    {
        height:50px;
        vertical-align: middle;
    }

    .logo-box
    {
        height: 60px;
        line-height: 60px;
        padding-left: 30px;
    }
    .tab-item-list
    {
        height: 100%;
        display: flex;
        margin-left: 100px;
    }
    .tab-item
    {
        height: 100%;
        transition: .6s;
        padding:0 30px;
    }

    .tab-item:hover
    {
        background-color: #BB43F9;
        cursor: pointer;
    }
    .tab-item-active
    {
        background-color: #BB43F9;
    }
    .userinfo-box
    {
        line-height: 60px;
        height: 100%;
        margin-left:600px;
    }
    .avatar
    {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        vertical-align: middle;
    }
    span{
        margin-left: 10px;
    }
</style>