<template>
    <div id="index-page">
          <div class="left-part">
              <el-col :span="12">
                    <el-menu
                    @select="tabChange"
                    default-active="1"
                    class="el-menu-vertical-demo"
                    @open="handleOpen"
                    @close="handleClose"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b">
                    <el-submenu index="1">
                        <template slot="title">
                        <i class="el-icon-location"></i>
                        <span>账号管理</span>
                        </template>
                        <el-menu-item-group>
                            <el-menu-item index="1-1">用户账号管理</el-menu-item>
                        </el-menu-item-group>

                    </el-submenu>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">公告管理</span>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-document"></i>
                        <span slot="title">训练教程管理</span>
                    </el-menu-item>
                    <!-- <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span slot="title">导航四</span>
                    </el-menu-item> -->
                    </el-menu>
                </el-col>
          </div>

          <div class="right-part">
              <!-- 面包屑导航 -->
              
              <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item v-for="item in routeList"
                    :key="item.name" :to="item.to">{{item.name}}</el-breadcrumb-item>
              </el-breadcrumb>
              
              <h2 v-show="welcomeShow">欢迎来到后台管理系统</h2>
              <!-- 面包屑导航结束 -->
              <router-view></router-view>
          </div>
    </div>
</template>

<script>
import TabBar from '../components/TabBar.vue'
export default {
    components:{
        
    },
    data(){
        return{
            routeList:[
                {
                    name:'首页',
                    to:"/index"
                },
            ],
            pathList:[
                '/index/user',
                '/index/bulletin',
                '/index/tutorial'
            ],
            welcomeShow:false,
        }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      tabChange(index,path)
      {
          if(path[0]=='1')
          {
              if(path[1]=='1-1')
              this.$router.push(this.pathList[0]);
              return;
          }
          this.$router.push(this.pathList[index-1]);
      }
    },
     created(){
          console.log(this.$route.path);
          if(this.$route.path=='/index')
          {
              console.log(this.$router.path);
              this.welcomeShow=true;
          }
      },
    watch:{
        //监听路由变化
        $route(to,from)
        {
            if(to.path!='/index')
            this.welcomeShow=false;
            else
            this.welcomeShow=true;
            console.log('router change');
            let index=this.routeList.findIndex((item)=>{
                return item.to==to.path;
            })
            
            if(index==-1)
            {
                this.routeList.push({
                name:to.meta.pathName,
                to:to.path
                })
            }
            else
            { 
                this.routeList.splice(index+1,this.routeList.length-1-index);
            }
            
        }
    }
}
</script>


<style scoped>
     #index-page
     {
         height: 100%;
         display: flex;
     }
     .main-container
     {
         width: 90%;
         margin:0 auto;
         background-color: #fff;
         min-height: 100%;
     }
     .left-part
     {
        width: 250px;
        height: 100%;
     }
     .right-part
     {
        flex:1;
        background-color: #fff;
        height: 100%;
        padding:20px;
        overflow: scroll;
     }
     .el-col
     {
         width: 100%;
         height: 100%;
         padding: 0;
     }
     .el-menu
     {
         height: 100%;
         margin:0;
     }
    .el-breadcrumb
    {
        width: 100%;
        height: 50px;
    }
</style>